<template>
  <ion-page>
    <Back />
    <Layout
      btnSrc="/registrPage"
      height="false"
      outlineBtn="."
      filledBtn="."
      title="Лицевой счёт"
      v-if="lcList?.length === 0"
    >
      <template v-slot:main-content>
        <ion-item router-link="/personalAccountNew">
          <ion-Icon
            class="icon-start"
            size="large"
            slot="start"
            :icon="pencilOutline"
          ></ion-Icon>
          <ion-text class="sub-title">Добавить лицевой счет</ion-text>
          <ion-icon
            class="icon-end"
            size="large"
            slot="end"
            :icon="chevronForwardOutline"
          ></ion-icon>
        </ion-item>
        <ion-item router-link="/personalAccountInfoSearch">
          <ion-Icon
            class="icon-start"
            size="large"
            slot="start"
            :icon="documentTextOutline"
          ></ion-Icon>
          <ion-text class="sub-title">Узнать лицевой счет</ion-text>
          <ion-icon
            size="large"
            class="icon-end"
            slot="end"
            :icon="chevronForwardOutline"
          ></ion-icon>
        </ion-item>
      </template>
    </Layout>
    <Layout
      btnSrc="/registrPage"
      height="false"
      outlineBtn="."
      filledBtn="."
      title="Лицевой счёт"
      v-else
    >
      <template v-slot:main-content>
        <ion-item lines="none">
          <ion-text
            ><p class="title ion-text-start">
              Лицевой счет {{ lcList[0]?.code }}
            </p></ion-text
          >
          <ion-icon slot="end" :icon="ellipsisVertical" />
        </ion-item>

        <ion-list>
          <ion-item>
            <ion-text>{{ lcList[0]?.name }}</ion-text>
          </ion-item>
          <ion-item>
            <ion-text>{{ lcList[0]?.address }}</ion-text>
          </ion-item>
          <ion-item>
            <ion-text> Задолженность: </ion-text>
            <ion-text slot="end" class="text-blue">{{
              lcList[0]?.debts?.accruals
            }}</ion-text>
          </ion-item>
          <ion-item>
            <ion-text> Пени: </ion-text>
            <ion-text class="text-blue" slot="end">{{
              lcList[0]?.debts?.penalties
              
            }}</ion-text>
          </ion-item>
        </ion-list>
        <Button
          class="btn"
          name="Оплата"
          router-link="/personalAccountPayment"
        />

        <Button
          class="btn"
          :outline="true"
          name="Внести показания"
          @click="
            () =>
              router.push({
                name: 'personalAccountIndication',
                params: { counterId: lcList[0]?.counters[0]?.counterId },
              })
          "
        />
        <Button class="btn" :outline="true" name="История платежей" />
      </template>
      <template v-slot:content>
        <div v-for="el in lcList" :key="el">
          <LayoutBox>
            <template v-slot:content>
              <ion-item lines="none">
                <ion-text
                  ><p class="title ion-text-start">
                    Лицевой счет {{ el.code }}
                  </p></ion-text
                >
                <ion-icon slot="end" :icon="ellipsisVertical" />
              </ion-item>

              <ion-list>
                <ion-item>
                  <ion-text>{{ el.name }}</ion-text>
                </ion-item>
                <ion-item>
                  <ion-text>{{ el.address }}</ion-text>
                </ion-item>
                <ion-item>
                  <ion-text> Задолженность: </ion-text>
                  <ion-text slot="end" class="text-blue">{{
                    el.debts?.accruals
                  }}</ion-text>
                </ion-item>
                <ion-item>
                  <ion-text> Пени: </ion-text>
                  <ion-text class="text-blue" slot="end">{{
                    el.debts?.penalties
                  }}</ion-text>
                </ion-item>
              </ion-list>
              <Button
                class="btn"
                name="Оплата"
                router-link="/personalAccountPayment"
              />

              <Button
                class="btn"
                :outline="true"
                name="Внести показания"
                router-link="/personalAccountIndication"
              />
              <Button class="btn" :outline="true" name="История платежей" />
            </template>
          </LayoutBox>
        </div>

        <div class="footer">
          <LayoutBox>
            <template v-slot:content>
              <ion-item router-link="/personalAccountNew">
                <ion-Icon
                  class="icon-start"
                  size="large"
                  slot="start"
                  :icon="pencilOutline"
                ></ion-Icon>
                <ion-text class="sub-title">Добавить лицевой счет</ion-text>
                <ion-icon
                  class="icon-end"
                  size="large"
                  slot="end"
                  :icon="chevronForwardOutline"
                ></ion-icon>
              </ion-item>
              <ion-item router-link="/personalAccountInfoSearch">
                <ion-Icon
                  class="icon-start"
                  size="large"
                  slot="start"
                  :icon="documentTextOutline"
                ></ion-Icon>
                <ion-text class="sub-title">Узнать лицевой счет</ion-text>
                <ion-icon
                  size="large"
                  class="icon-end"
                  slot="end"
                  :icon="chevronForwardOutline"
                ></ion-icon>
              </ion-item>
            </template>
          </LayoutBox>
        </div>
      </template>
    </Layout>
  </ion-page>
</template>


<script>
import { defineComponent } from "vue";
import { useRouter } from "vue-router";
import Layout from "../components/Layout.vue";
import { IonPage, IonText, IonItem, IonIcon } from "@ionic/vue";
import {
  pencilOutline,
  documentTextOutline,
  chevronForwardOutline,
  ellipsisVertical,
} from "ionicons/icons";
import Back from "../components/Back.vue";
import LayoutBox from "../components/LayoutBox.vue";
import Button from "../components/Button.vue";
import { mapActions } from "pinia";
import { usePersonalAccountStore } from "../stores/personalAccount";

export default defineComponent({
  name: "personalAccaountPage",
  components: {
    Back,
    IonPage,
    Layout,
    IonText,
    IonItem,
    IonIcon,
    LayoutBox,
    Button,
  },
  methods: {
    ...mapActions(usePersonalAccountStore, ["getAccount"]),
  },
  computed: {
    lcList() {
      return this.$pinia.state.value?.personalAccount?.getAccountResponse?.data
        ? this.$pinia.state.value?.personalAccount?.getAccountResponse?.data
        : [];
    },
  },
  mounted() {
    this.getAccount();
    const getAccountResponse =
      this.$pinia.state.value?.personalAccount?.getAccountResponse;
    if (getAccountResponse?.error === false) {
      // this.$data.lcList = getAccountResponse.data;
      console.log(getAccountResponse, "testgetAccount");
    }
    console.log(getAccountResponse, "testgetAccount");
  },
  data() {
    return {
      data: {
        number: "№123456789",
        name: "Иванов Иван Иванович",
        address: "Якутск, ул. Автодорожная 11/4",
        debt: "-680,92 руб.",
        penalties: "0 руб.",
      },
      // lcList: [],
    };
  },
  setup() {
    const router = useRouter();
    return {
      router,
      pencilOutline,
      documentTextOutline,
      chevronForwardOutline,
      ellipsisVertical,
    };
  },
});
</script>

<style scoped>
ion-icon {
  width: 32px;
  height: 32px;
  color: #0378b4;
  --ionicon-stroke-width: 35px;
}

.icon-start {
  margin-right: 20px;
}
.icon-end {
  width: 24px;
  height: 24px;
  margin-left: 0px;
}

.text-blue {
  color: #0378b4;
  font-weight: 700;
  margin-left: 0;
}
ion-icon {
  width: 20px;
  height: 20px;
  color: #0378b4;
  margin-left: 0;
}
.icon-start {
  width: 32px;
  height: 32px;
  margin-right: 20px;
}
.icon-end {
  width: 24px;
  height: 24px;
  margin-left: 0px;
}

.btn {
  margin-bottom: 15px;
}
.footer {
  width: 100%;
  margin-top: 30px;
  height: 100%;
}
</style>